@charset "UTF-8";
.ui-group {
  position: relative;
  margin-bottom: 20px;
}

.ui-group label {
  display: block;
  margin-bottom: 10px;
}

.ui-group input, .ui-group textarea {
  display: block;
  width: 100%;
  -webkit-appearance: none;
  background: white;
  color: #999;
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  padding: 12px 15px;
}

.ui-group textarea {
  resize: vertical;
}

.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #000;
}

.ui-checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.ui-checkbox label {
  width: 100%;
  position: relative;
  cursor: pointer;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 20px;
  background-color: white;
  border: 1px solid #AFB7C0;
  border-radius: 4px;
  cursor: pointer;
  vertical-align: top;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 12px;
  border-radius: 50%;
  background-image: url("../../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  transition: transform .2s ease;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 14px;
  cursor: pointer;
}

