@import "utilities/variables";
@import "utilities/mixins";

@import "elements/ui";

@import "vendor/selectric";

@import "components/signIn";
@import "components/signUp";


// SIGN IN/UP
.signup,
.signin {
  padding: 0 !important;

  &__wrapper {
    display: flex;
    width: 100%;
    min-height: 100vh;
    @include r(767) {
      flex-wrap: wrap; } }

  &__left {
    @include r(767) {
      width: 100%;
      padding-left: 20px;
      padding-right: 20px; }
    @include rmin(768) {
      padding: 15px;
      width: 50%; }
    @include rmin(992) {
      padding: 0;
      width: 576px; }
    @include rmin(1440) {
      width: 40%; } }

  &__right {
    display: flex;
    align-items: center;
    background-color: $colorBlack;
    background-image: url("../img/img-bg__sign.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @include r(767) {
      width: 100%; }
    @include rmin(768) {
      width: 50%; }
    @include rmin(992) {
      width: calc(100% - 576px); }
    @include rmin(1440) {
      width: calc(100% - 40%); } }

  &__logo {
    padding-top: 40px;
    padding-left: 48px;
    padding-bottom: 50px;
    @include r(575) {
      padding-left: 0; }
    @include rmin(768) {
      padding-left: 0; }
    @include rmin(992) {
      padding-left: 48px; } }

  &__title {
    text-align: left; }

  &__text {
    padding-bottom: 50px;
    text-align: left;

    a {
      color: $colorAlizarin; } }

  &__form {
    width: 100%;
    max-width: 397px;
    margin-left: auto;
    margin-right: auto; }

  .form {
    &__btn {
      max-width: 100%;
      margin-bottom: 117px; } } }

// SIGN
.sign {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 81px 76px;
    @include r(767) {
      padding: 20px; }
    @include rmin(768) {
      padding: 20px; }
    @include rmin(992) {
      padding: 30px; }
    @include rmin(1200) {
      padding: 51px 46px; }
    @include rmin(1366) {
      padding: 81px 76px; } }

  &__col {
    width: 50%;
    padding-right: 30px;
    margin-bottom: 60px;
    font-family: "SF Pro Text", sans-serif;
    @include r(575) {
      width: 100%;
      padding-right: 0; }
    @include rmin(768) {
      width: 100%;
      padding-right: 0; }
    @include rmin(1200) {
      width: 50%;
      padding-right: 30px; }

    &-header {
      display: flex;
      align-items: center;
      padding-bottom: 24px; }

    &-img {
      width: 90px;

      img {
        display: block;
        max-width: 100%;
        height: auto; } }

    &-title {
      width: calc(100% - 100px);
      color: $colorWhite;
      font-size: 18px;
      font-weight: 600;
      @include r(767) {
        font-size: 16px; } }

    &-list {
      list-style: none;
      width: 100%;
      max-width: 330px;
      padding-left: 1em;
      @include r(575) {
        max-width: 100%; } }

    &-item {
      padding-bottom: 10px;
      color: rgba($colorWhite, 0.7);
      font-size: 16px;
      @include r(767) {
        font-size: 14px; }

      &::before {
        content: "•";
        display: inline-block;
        width: 1em;
        margin-left: -1em;
        color: $colorWhite; } } } }
