// UI


// WORKS KIND OF bootstrap - group have label and input
.ui-group {
  position: relative;
  margin-bottom: 20px;
  label {
    display: block;
    margin-bottom: 10px; }
  input, textarea {
    display: block;
    width: 100%;
    -webkit-appearance: none;
    background: white;
    color: $grayColor;
    border: 1px solid $borderColor;
    border-radius: 4px;
    padding: 12px 15px; }
  textarea {
    resize: vertical; } }


// Checkbox
.ui-checkbox {
  input {
    display: none;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    &:checked {
      + label {
        &::before {
          border-color: $colorBlack; }
        &::after {
          transform: translateY(-50%) scale(1); } } } }
  label {
    //display: block
    width: 100%;
    position: relative;
    cursor: pointer;
    &::before {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      content: ' ';
      size: 20px;
      background-color: white;
      border: 1px solid #AFB7C0;
      border-radius: 4px;
      cursor: pointer;
      vertical-align: top;
      cursor: pointer;
      transition: all $transitionSpeed ease-in-out; }
    &::after {
      position: absolute;
      left: 5px;
      top: 50%;
      transform: translateY(-50%) scale(0);
      display: inline-block;
      content: ' ';
      size: 12px;
      //background: $colorBlack
      border-radius: 50%;
      background-image: url('../../img/tick.svg');
      background-repeat: no-repeat;
      background-position: center;
      transition: transform .2s ease; }
    span {
      display: inline-block;
      padding-left: 35px;
      vertical-align: top;
      line-height: 1.3;
      font-size: 14px;
      cursor: pointer; } } }
